export enum EventsType {
  stepChange = 'STEP_CHANGE',
  stepFlows = 'STEPS_FLOW',
  setCustomerId = 'SET_CUSTOMER_ID',
  stepResult = 'OPERATION_RESULT',
  asset = 'ASSET',
}

export enum TypeFamily {
  onboarding = 'ONBOARDING',
  authentication = 'AUTHENTICATION',
}

export enum TypePayload {
  device = 'DEVICE',
  coordinates = 'COORDINATES',
  result = 'OPERATION_RESULT',
}

export enum TrackingStatus {
  cancelled = 'CANCELLED',
  expired = 'EXPIRED',
  denied = 'DENIED',
  blacklisted = 'BLACKLISTED',
  started = 'STARTED',
  succeeded = 'SUCCEEDED',
}

export enum TrackingReason {
  selphidInternalError = 'SELPHID_INTERNAL_ERROR',
  selphidCancelUser = 'SELPHID_CANCEL_BY_USER',
  selphidTimeout = 'SELPHID_TIMEOUT',
  selphiInternalError = 'SELPHI_INTERNAL_ERROR',
  selphiCancelUser = 'SELPHI_CANCEL_BY_USER',
  selphiTimeout = 'SELPHI_TIMEOUT',
  agoraInternalError = 'AGORA_INTERNAL_ERROR',
  agoraCancelUser = 'AGORA_CANCEL_BY_USER',
  cancelUser = 'CANCEL_BY_USER',
  videoRecruitmentCancelUser = 'VIDEO_RECORDING_CANCEL_BY_USER',
}

export enum TrackingSteps {
  start = 'START',
  selphiWidget = 'SELPHI_WIDGET',
  selphidWidget = 'SELPHID_WIDGET',
  videoRecruitmentWidget = 'VIDEO_CONTRACTING',
  finish = 'FINISH',
}

export type TrackingPayloadDevice = {
  type: TypePayload;
  model: string;
  osVersion: string;
  brand: string;
  deviceId: string;
};

export type TrackingEvent = {
  eventId: string;
  clientTimestamp: string;
  executionTime?: null;
  payload: TrackingPayloadDevice;
};

export type TrackingData = {
  version: string;
  operationId: string;
  tenantId: string;
  sessionId: string;
  source: string;
  family: string;
  events: TrackingEvent;
};

export type TrackingOptions = {
  trackingUrl?: string;
  trackingUrlAssets?: string;
  clientId: string;
  authUrl?: string;
  clientSecret: string;
  tenantId: string;
  type?: TypeFamily;
  withAuth?: boolean;
  disabled?: boolean;
};

export type ExportData = {
  operationId: string;
  sessionId: string;
  extraData: string;
};

export type BaseVideoSettings = {
  operationId: string;
  tenantId?: string;
};

export type VideoSettings = {
  video: boolean;
} & BaseVideoSettings;

export type videoRecruitmentSettings = {
  videoRecruitment: boolean;
} & BaseVideoSettings;
